<script lang="typescript">import Copyable from "./Copyable.svelte";
import Hoverable from "./Hoverable.svelte";
export let style = "";
export let dataCy = null;
export let value;
export let notificationText = "Copied to your clipboard";
export let truncate = false;
export let expandable = true;
const [head, tail] = value.split(/(.{8}).*(.{8})/).filter(Boolean);
let hover = false;
//# sourceMappingURL=StyledCopyable.svelte.js.map</script>

<style>
  .wrapper {
    display: flex;
    position: relative;
  }
</style>

<Hoverable bind:hovering={hover}>
  <div class="wrapper" {style} data-cy={dataCy}>
    <Copyable
      style="align-items: center; color: var(--color-foreground-level-6)"
      copyContent={value}
      {notificationText}
      styleContent={hover}
      showIcon={true}>
      {#if !truncate || (expandable && hover)}
        <p class="typo-text-small-mono">{value}</p>
      {:else}
        <p class="typo-text-small-mono">{head}…{tail}</p>
      {/if}
    </Copyable>
  </div>
</Hoverable>
